.card {
    max-width: 200px;
    height: 325px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 10px;
    background-color: #8ECAE6;
    color:#023047;
}

h4 {
    margin-block: 5px;
    text-align: center;
}

.card-img {
    border-bottom: 2px solid grey;
    border-top: 2px solid grey;
}

img {
    width: 100%;
    height: 150px;
}

.card-description {
    margin-inline: 1rem;
    text-align: left;
}

.kosarba-tesz {
    display: flex;
    flex-direction: row;
    height: max-content;
    margin-top: 2rem;

}

h6 {
    font-size: 14px;
    text-align: right;
    margin: 0;
    width: 100%;
}

button {
    width: 110px;
    height: 25px;
    background: none;
    background-color: #219EBC;
    border-radius: 7px;
    border: 1px solid rgb(153, 153, 153);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    cursor: pointer;
    color:023047
}

button:hover {
    border-color: #023047;
    background-color: transparent;
    color: #023047;
    transition: 0.5s;
}